*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html:focus-within {
  scroll-behavior: smooth;
}

body {
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

img,
picture,
svg {
  max-width: 100%;
  display: block;
}

button {
  font: inherit;
}

ul[role=list],
ol[role=list] {
  list-style: none;
}

.container {
  width: min(68.5rem, 100% - 2rem);
  margin-inline: auto;
  display: flex;
}

.container--wide {
  width: min(82.5rem, 100% - 2rem);
  margin-inline: auto;
  display: flex;
}

a {
  text-decoration: none;
  color: #fff;
}

html {
  font-family: "Roboto", sans-serif;
}

h1,
h2,
h3 {
  font-weight: 700;
}

p {
  font-weight: 300;
}

.header {
  display: flex;
  flex-direction: column;
  background-color: #b50014;
}
.header .header__nav {
  width: 100%;
  max-height: 6rem;
  display: flex;
  justify-content: space-between;
  font-weight: 700;
  position: relative;
}
.header .header__nav .logo {
  max-width: 11rem;
  z-index: 999;
  position: absolute;
}
@media only screen and (max-width: 40em) {
  .header .header__nav .logo {
    max-width: 6rem;
  }
}
.header .header__nav .navigation {
  display: flex;
  justify-content: flex-end;
  flex: 1;
  align-items: center;
  gap: 2.5rem;
  padding-block: 2rem;
}
.header .header__nav .navigation a:hover {
  color: #252525;
}
.header .header__nav .navigation a:hover path {
  fill: #252525;
}
.header .header__nav .navigation .download {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.header .header__nav .navigation .menu {
  display: none;
}
@media only screen and (max-width: 40em) {
  .header .header__nav .navigation .nav-link {
    display: none;
  }
  .header .header__nav .navigation .menu {
    display: block;
  }
}
.header .working-hours {
  background-color: #252525;
  color: #fff;
  display: flex;
}
.header .working-hours .hours {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-size: 0.875rem;
  padding-block: 0.4rem;
  gap: 0.4rem;
}

